            pattern: "",
            patternItems:[{
                text: ""
            }],
            markSettingDialog: false,
            deleteItem: false,
            radioGroup: "",
            items: [
                {
                text: '',
                disabled: false,
                to: "/projects",
                exact: true
                },
                {
                text: '',
                disabled: true
                },
            ],
            test: [{
                type: 'radio',
                value: '2'
            }, {
                type: 'textarea',
                value: '2'
            }, {
                type: 'field',
                value: '2'
            }]
        }
    },
    methods: {
        clickDeleteItem() {
            this.deleteItem = false
        },
        addMultipleItems() {
            this.multipleItems.push({value: ""})
        },
        removeMultipleItems(index) {
            this.multipleItems.splice(index, 1);
        },
        down(index) {
            const tmp = this.test[index]
            this.test.splice(index, 1)
            this.test.splice(index-1, 0, tmp)
        },
        top(index) {
            const tmp = this.test[index]
            this.test.splice(index, 1)
            this.test.splice(index+1, 0, tmp)
        },
        changeSelfType() {
            if(this.selfType == "") {
                this.pattern = ""
                this.patternItems = [{
                    text: ""
                }]
            }
            if (this.selfType == "") {
                this.pattern = ""
                this.patternItems = [{
                    text: ""
                }, {
                    text: ""
                }]
            }
        }
        
    }
}
